import { createClient } from "@supabase/supabase-js";
import type { SupabaseClient, User } from "@supabase/supabase-js";
import { HttpError } from "./api-respond";

function getCookieValue(cookieHeader: string | null, name: string) {
  if (!cookieHeader) return null;
  const parts = cookieHeader.split(";");
  const prefix = `${name}=`;

  for (const part of parts) {
    const trimmed = part.trim();
    if (trimmed.startsWith(prefix)) {
      return decodeURIComponent(trimmed.substring(prefix.length));
    }
  }
  return null;
}

function extractAccessToken(req: Request): string | null {
  const authHeader = req.headers.get("authorization");
  if (authHeader?.startsWith("Bearer ")) {
    return authHeader.substring(7).trim();
  }

  const cookieHeader = req.headers.get("cookie");
  const cookieToken =
    getCookieValue(cookieHeader, "sb-access-token") ??
    getCookieValue(cookieHeader, "sb:token");

  return cookieToken ?? null;
}

export async function getSupabaseFromRequest(req: Request): Promise<{
  supabase: SupabaseClient;
  user: User;
}> {
  const url = process.env.NEXT_PUBLIC_SUPABASE_URL!;
  const key = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!;
  const accessToken = extractAccessToken(req);

  const headers: Record<string, string> = {};
  if (accessToken) {
    headers["Authorization"] = `Bearer ${accessToken}`;
  }

  const supabase = createClient(url, key, {
    auth: { persistSession: false, detectSessionInUrl: false },
    global: {
      headers,
    },
  });

  const {
    data: { user },
    error,
  } = await supabase.auth.getUser(accessToken ?? undefined);

  if (error) {
    throw new HttpError(401, "UNAUTHORIZED", "Not authenticated", error);
  }

  if (!user) {
    throw new HttpError(401, "UNAUTHORIZED", "Not authenticated");
  }

  return { supabase, user };
}

export async function requireUser(req: Request): Promise<{
  supabase: SupabaseClient;
  user: User;
}> {
  const { supabase, user } = await getSupabaseFromRequest(req);
  if (!user) {
    throw new HttpError(401, "UNAUTHORIZED", "Not authenticated");
  }
  return { supabase, user };
}

export async function requireAdmin(req: Request): Promise<{
  supabase: SupabaseClient;
  user: User;
}> {
  const { supabase, user } = await requireUser(req);

  const { data: profile, error } = await supabase
    .from("profiles")
    .select("is_admin")
    .eq("id", user.id)
    .single();

  if (error || !profile?.is_admin) {
    throw new HttpError(403, "FORBIDDEN", "Admin access required", error);
  }

  return { supabase, user };
}
