import { requireUser } from "@/lib/auth-guard";
import { ok, bad } from "@/lib/api-respond";
import { computeNextZonesForDriver } from "@/lib/server/recommendation";
import { isValidLatitude, isValidLongitude } from "@/lib/server/geo-utils";

const CACHE_TTL_MS = 60_000;

export async function GET(req: Request) {
  try {
    const { supabase, user } = await requireUser(req);
    const url = new URL(req.url);

    const latParam = url.searchParams.get("lat");
    const lonParam = url.searchParams.get("lon");
    const kParam = url.searchParams.get("k");
    const k = Math.min(Math.max(parseInt(kParam ?? "3", 10) || 3, 1), 10);

    let lat: number | null = null;
    let lon: number | null = null;
    let snappedZoneId: number | null = null;
    let speedKmh: number | null = null;
    let source: "live" | "cached" = "live";

    let driverState:
      | {
          lat: number;
          lon: number;
          speed_kmh: number | null;
          snapped_zone_id: number | null;
          recommendation: any;
          updated_at: string;
        }
      | null = null;

    if (latParam && lonParam) {
      lat = Number(latParam);
      lon = Number(lonParam);
      if (!Number.isFinite(lat) || !Number.isFinite(lon)) {
        return bad(
          "Invalid coordinates",
          { lat: latParam, lon: lonParam },
          400,
          "INVALID_INPUT"
        );
      }
      if (!isValidLatitude(lat) || !isValidLongitude(lon)) {
        return bad(
          "Coordinates out of range",
          { lat, lon },
          400,
          "INVALID_INPUT"
        );
      }
    } else {
      const { data, error } = await supabase
        .from("driver_state")
        .select(
          "lat,lon,speed_kmh,snapped_zone_id,recommendation,updated_at"
        )
        .eq("driver_id", user.id)
        .maybeSingle();

      if (error && error.code !== "PGRST116") {
        console.error("[next-spot] driver_state fetch failed", {
          driverId: user.id,
          error,
        });
        return bad("Failed to fetch driver state", null, 500, "DB_ERROR");
      }

      if (!data) {
        return bad(
          "No location data available",
          { hint: "Send a ping first or provide lat/lon query params" },
          400,
          "INVALID_INPUT"
        );
      }

      driverState = data;
      lat = data.lat;
      lon = data.lon;
      speedKmh = data.speed_kmh;
      snappedZoneId = data.snapped_zone_id;
    }

    if (lat === null || lon === null) {
      return bad("Missing coordinates", null, 400, "INVALID_INPUT");
    }

    if (!latParam && !lonParam && driverState?.recommendation) {
      const updatedAt = new Date(driverState.updated_at).getTime();
      if (Date.now() - updatedAt < CACHE_TTL_MS) {
        source = "cached";
        return ok({
          computed_at:
            driverState.recommendation.computed_at ?? driverState.updated_at,
          source,
          context: driverState.recommendation.context ?? null,
          top: driverState.recommendation.top ?? [],
        });
      }
    }

    if (!snappedZoneId) {
      const { data: zoneData, error: zoneError } = await supabase.rpc(
        "nearest_zone",
        {
          p_lon: lon,
          p_lat: lat,
          p_max_m: 3000,
        }
      );

      if (zoneError) {
        console.error("[next-spot] nearest_zone failed", {
          driverId: user.id,
          error: zoneError,
        });
      } else if (typeof zoneData === "number") {
        snappedZoneId = zoneData;
      }
    }

    const recommendation = await computeNextZonesForDriver({
      supabase,
      driverId: user.id,
      currentLoc: { lat, lon },
      snappedZoneId,
      now: new Date(),
      currentSpeedKmh: speedKmh ?? undefined,
      k,
    });

    return ok({
      computed_at: recommendation.computed_at,
      source,
      context: recommendation.context,
      top: recommendation.top,
    });
  } catch (error: any) {
    if (error.message === "Unauthorized") {
      return bad("Unauthorized", null, 401, "UNAUTHORIZED");
    }
    console.error("[next-spot] unexpected error", error);
    return bad("Unexpected error", null, 500, "INTERNAL_ERROR");
  }
}
